:root {
    /* COLOR */
    --color-home: #05C4C2;
    --color-about: #FFC300;
    --color-portfolio: #c4c4c4;
    --color-contact: #222222;
    --text-color-light: #ffffff;
    --text-color-dark: #464646;

    /* SHADOW */
    --box-shadow: 0 10px 20px 0 rgba(0,0,0,0.25);
    --text-shadow: 0 10px 10px rgba(0,0,0,0.5);

    /* TEXT */
    --font-family: "Quicksand", sans-serif;
    --font-weight: 300;
    --font-size-super-large: 60px;
    --font-size-very-large: 48px;
    --font-size-large: 30px;
    --font-size-medium-large: 24px;
    --font-size-medium: 20px;
    --font-size-medium-small: 18px;
    --font-size-small: 14px;
    --font-size-very-small: 12px;
    --font-size-super-small: 8px;

    /* CARDS */
    --card-border-radius: 10px;
}